import { Injectable } from '@angular/core';
/* To v lu h s cng nh h s hin ti
 khi xut n ly h s hin ti ra  s dng
*/
import { HOSO } from './hoso.model';
import { HoSoMacDinh } from './hoso.macdinh';
import { Storage } from '@ionic/storage-angular';
import { GetExcelDataService } from './get-excel-data.service';
import { CAIDAT } from './CAIDAT';
import { MenuTagsService } from './menuTags/menu-tags.service';
import { CongcuforallTsService } from "./congcuforall.ts.service";
@Injectable({
  providedIn: 'root',
})
export class HosoService {
  skey = CAIDAT.skey;
  hosomacdinh = HoSoMacDinh;
  danhsachhoso: HOSO[] = [];
  filterBigListForSaveString: string[] = [];
  filterBigList: string[][] = [];
  private _storage: Storage | null = null;
  /** y s load h s mc nh khi xut file
   * Mi h s s c 1 google sheet lun, c th bm vo  chy lun
   */
  constructor(
    private storage: Storage,
    private getExcelDataService: GetExcelDataService,
    private menuTagsService: MenuTagsService,
    private congcu: CongcuforallTsService
  ) {
    // console.log(JSON.stringify(this.hosomacdinh).length)
    // for (let index = 0; index < 30; index++) {
    //   let x = {...this.hosomacdinh}
    //   x.name = "test name" + " " + index
    //   this.danhsachhoso.push(x);
    // }
    this.init();
  }
  async init() {
    const storage = await this.storage.create();
    this._storage = storage;
    let countHoso = 0;
    await this._storage.forEach((value : HOSO, key, index) => {
      // console.log(key)
      // console.log(value)
      if (key.startsWith(this.skey)) {
        value.creatingAnki = false;
        this.addHosoVaoDanhSach(value);
        countHoso += 1;
        // console.log(countHoso)
      }
    });
    if (countHoso == 0) {
      // ln u load web
      this.addNewHoSo(this.hosomacdinh);
      console.log(countHoso);
    }
  }
  /**
   * Check id  thm id, check createdAt  thm.
   * Lu vo storage
   * @param hoso
   */
  async addNewHoSo(hoso: HOSO, source?: string) {
    // console.log(hoso);
    hoso = await this.xulytruocaddhoso(hoso);

    // console.log(hoso)
    // @ts-ignore
    return await this._storage.get(hoso.id).then(async (re) => {
      if (re == null) {
        this.danhsachhoso.unshift(hoso);
        // this._storage.set(hoso.id, hoso)

        // Nu h s, ti v  update, khc tn; xa tn c
        // console.log(hoso)
        if (hoso.id != hoso.shared?.oldId && hoso.shared?.oldId) {
          try {
            this.deleteHoSo(hoso.shared?.oldId);
          } catch (error) {
            console.log(error);
          }
        }
        await this.getExcelDataService.getExcelColumns(hoso);
        this.updateHoSo(hoso, CAIDAT.hoso.savenewhoso);

        return true;
      } else {
        // Trng hp ging tn c, tc storage id n ging
        //source == CAIDAT.share.newhosofromgist : import t trn gist xung, nhng l update t url (tc l danhsachhoso  c ci ny ri)
        if (
          hoso.id == hoso.shared?.oldId ||
          source == CAIDAT.share.newhosofromgist ||
          source == CAIDAT.hoso.saveFromSyncServer
        ) {
          try {
            this.updateHoSo(hoso, CAIDAT.hoso.saveFromDownloadUpdate);
            let indx = -1;
            this.danhsachhoso.map((val, ind) => {
              val.id == hoso.id ? (indx = ind) : false;
            });
            this.danhsachhoso[indx] = hoso;
            return true;
          } catch (error) {
            console.log(error);
          }
        }
        return false;
      }
    });
  }
  public getHoSo(key: string) {
    //@ts-ignore
    return this._storage.get(key);
  }
  deleteHoSo(hosoId: string | undefined) {
    var removeIndex = this.danhsachhoso.map((item) => item.id).indexOf(hosoId);
    ~removeIndex && this.danhsachhoso.splice(removeIndex, 1);
    console.info('Xa h s ', removeIndex, hosoId);
    // Xa lun chia s?
    //@ts-ignore
    return this._storage.remove(hosoId);
  }
  async updateHoSo(hoso: HOSO, source: string) {
    // Ion destroy view s gi hm ny, nn bo li mi
    if (hoso != undefined) {
      // Kim tra h s c cp nht g mi khng? Nu c mi, th update, cn khng th ngh
      //@ts-ignore
      /* await this._storage?.get(hoso.id).then((reHoSo) => {
        console.log(hoso, reHoSo)
        if ( !this.objectsAreDifferent(hoso, reHoSo) ) {
          console.log("h s khng sa");
          return false;
        }
      }) */

      try {
        //@ts-ignore
        await this._storage.remove(hoso.id);
        console.log("update h s");
      } catch (error) {}
      // To id mi: theo tn mi
      hoso = await this.xulytruocaddhoso(hoso);
      //@ts-ignore
      await this._storage.set(hoso.id, hoso);
      // console.info(" lu h s")
    }
  }
  async isHoSoExist(hoso: HOSO) {
    //@ts-ignore
    await this._storage.get(hoso.id);
  }
  //@ts-ignore
  addHosoVaoDanhSach(hoso) {
    this.danhsachhoso.push(hoso);
    // this.danhsachhoso$.next(hoso)
  }
  /**
   * To id, to createdat
   * @param hoso nhn h s
   * @returns Tr v h s  chnh sa id
   */
  async xulytruocaddhoso(hoso: HOSO) {
    // let crtime = new Date().getTime()

    hoso.id = this.skey + hoso.name;

    // hoso.updatedAt = crtime
    // if (hoso.createdAt == null) {
    //   hoso.createdAt = crtime
    // }
    if (hoso.sync == undefined) {
      hoso.sync = {
        type: 'open',
        lastSync: undefined,
      };
    }
    await this.congcu.currentTimeFromWorld().then((utcDatetime) => {
      //@ts-ignore
      hoso.sync.lastSync = utcDatetime;
    })
    return hoso;
  }
  filterHoSo(searchTerm: string) {
    return this.danhsachhoso.filter((data) => {
      return (
        JSON.stringify({ n: data.name, d: data.description })
          .toLowerCase()
          .indexOf(searchTerm.toLowerCase()) > -1
      );
    });
  }
  filterHoSoByTag(filterList: string[]) {
    // console.log(this.danhsachhoso)
    return this.danhsachhoso.filter((data) => {
      // console.log(data.tags)
      let isTrue = true;
      if (data.tags) {
        let tags = JSON.stringify(Array.from(data.tags)).toLowerCase();
        filterList.map((val) => {
          isTrue = isTrue && tags.indexOf(val.toLowerCase()) > -1;
        });
        return isTrue;
      } else {
        data.tags = new Set();
        return false;
      }
    });
  }
  filterHoSoByTagAND(filterList: string[]) {
    // console.log(this.danhsachhoso)
    // Nu yu cu ton b h s
    if (filterList.length == this.menuTagsService.tagsList.size) {
      return this.danhsachhoso;
    }
    return this.danhsachhoso.filter((data) => {
      // console.log(data.tags)
      let isHave = false;
      if (data.tags) {
        let tags = JSON.stringify(Array.from(data.tags)).toLowerCase();

        filterList.map((val) => {
          isHave = tags.indexOf(val.toLowerCase()) > -1;
        });
      }
      return isHave;
    });
  }
  
}
